.dark {
	padding: 20px;
	background: $dark; }

.btn {
	display: inline-block;
	vertical-align: top;
	border: 1px solid $dark;
	height: 35px;
	color: $dark;
	background: none;
	font-family: $montserrat;
	@include f(16px, 300, normal);
	letter-spacing: -0.02px;
	text-align: center;
	text-transform: uppercase;
	transition: color 0.3s, background 0.3s;
	&_white {
		border-color: $white;
		color: $white;
		@include hover {
			color: $dark;
			background: $white; } }
	&_sm {
		width: 180px; } }


.btn-text {
	position: relative;
	display: inline-block;
	vertical-align: top;
	padding-right: 14px;
	color: currentColor;
	background: none;
	font-family: $montserrat;
	@include f(12px, 700, normal);
	letter-spacing: 0;
	text-transform: uppercase;
	text-decoration: none;
	transition: color 0.25s;
	@include hover {
		color: $yellow; }
	&:before {
		position: absolute;
		content: "";
		top: -10px;
		left: -10px;
		right: -10px;
		bottom: -10px; }
	&_pink {
		@include hover {
			color: $pink; } }
	.icon-arrow-right {
		position: absolute;
		top: 50%;
		right: 0;
		margin-top: -0.5em;
		fill: $pink;
		font-size: 9px; } }
