.footer {
	padding: 54px 0;
	color: $white;
	background-image: linear-gradient(229deg, #36424D 3%, #2B333C 100%);
	border-bottom: 5px solid $black;
	&__wrap {
		width: 100%;
		display: table;
		@include sm {
			display: block; } }
	&__contacts,
	&__button {
		vertical-align: middle;
		display: table-cell;
		@include sm {
			display: block; } }
	&__button {
		text-align: right;
		@include sm {
			margin-top: 20px;
			text-align: center; } }
	&__info {
		vertical-align: top;
		display: inline-block;
		@include sm {
			display: block;
			padding-bottom: 22px; }
		&:not(:last-child) {
			margin-right: 50px;
			@include sm {
				margin: 0; } } }
	&__icon {
		margin-bottom: 7px; }
	&__subtitle {
		font-family: $montserrat;
		@include f(16px, 700, 1.4);
		letter-spacing: 0.46px; }
	&__meta {
		@include f(15px, 400, 20px);
		letter-spacing: -0.02px;
		a {
			color: inherit;
			transition: color 0.25s;
			@include hover {
				color: $yellow; } } }
	&__subnav {
		margin: 69px -13px 0;
		@include md {
			margin-top: 13px; } } }
