.projects {
	&_gray {
		background-color: rgba($bgGray, 0.1); }
	&__out {
		padding: 102px 0 117px; }
	&__head {
		padding: 90px 0;
		width: 100%;
		display: table;
		@include sm {
			padding: 40px 0;
			display: block; } }
	&__titles,
	&__all {
		vertical-align: middle;
		display: table-cell;
		@include sm {
			display: block; } }
	&__all {
		text-align: right;
		@include sm {
			margin-top: 20px;
			text-align: left; } }
	&__label {
		margin-bottom: -14px;
		font-family: $montserrat;
		font-weight: 200;
		font-size: 28px;
		color: $limedSpruce;
		text-transform: uppercase;
		letter-spacing: 4.67px;
		line-height: 0.9;
		@include sm {
			margin-bottom: -7px;
			font-size: 12px;
			line-height: 1;
			letter-spacing: 2.5px; } }
	&__wrap {
		padding-top: 28px;
		@include sm {
			padding-top: 0; } }
	&__row {
		margin: -28px 0;
		@include sm {
			margin: 0; }
		&:not(:first-child) {
			@include sm {
				margin-top: 30px; }
			&:last-child {
				margin-bottom: -40px;
				@include sm {
					margin-bottom: 0; } } } }
	&__field {
		&:not(:first-child) {
			margin-top: 80px; } } }
.project {
	margin: 0 -19px;
	width: calc(100% + 38px);
	table-layout: fixed;
	display: table;
	@include sm {
		display: flex;
		flex-wrap: wrap; }
	&__img-wrap {
		position: relative;
		z-index: 1;
		padding: 0 19px;
		vertical-align: top;
		display: table-cell;
		@include sm {
			order: 2;
			width: 100%;
			display: block; } }
	&__overlay {
		position: absolute;
		z-index: -1;
		top: 49px;
		right: 13.5%;
		bottom: -49px;
		width: 109%;
		background-color: $gallery;
		@include md {
			top: 29px;
			bottom: -29px; }
		@include sm {
			display: none; }
		&_top-right {
			top: -40px;
			left: 13.5%;
			right: auto;
			bottom: 40px;
			@include md {
				top: -20px;
				bottom: 40px; } } }
	&__img {
		height: 0;
		padding-bottom: 65.38%;
		background-repeat: no-repeat;
		background-size: cover; }
	&__wrap {
		padding: 28px 19px;
		text-align: center;
		vertical-align: middle;
		display: table-cell;
		@include sm {
			margin-bottom: 20px;
			padding: 0 19px;
			width: 100%;
			order: 1;
			text-align: left;
			display: block; }
		.projects__row:first-child & {
			padding-bottom: 9%;
			@include sm {
				padding-bottom: 0; } }
		.projects__row:last-child & {
			padding-top: 5%;
			@include sm {
				padding-bottom: 0; } }
		.projects__row:nth-child(odd) & {
			padding-bottom: 5%;
			@include sm {
				padding-bottom: 0; } }
		.projects__row:nth-child(even) & {
			padding-top: 5%;
			@include sm {
				padding-bottom: 0; } } }
	&__name {
		position: relative;
		padding-left: 21px;
		font-family: $arbutus;
		font-size: 30px;
		color: #3F566E;
		letter-spacing: 0;
		line-height: 1.2;
		transition: color .3s;
		vertical-align: top;
		display: inline-block;
		@include md {
			font-size: 24px; }
		@include sm {
			font-size: 18px; }
		&:hover {
			color: $yellow; }
		.icon-arrow-right {
			margin-left: 8px;
			font-size: 9px;
			vertical-align: middle; } }
	&__line {
		position: absolute;
		top: 0;
		left: 0;
		bottom: 0;
		width: 4px;
		background-color: $yellow;
		&_pink {
			background-color: $pink; } } }
