.inner {
  position: relative;
  padding-left: 15px;
  padding-right: 15px;
  margin-left: auto;
  margin-right: auto;
  max-width: 1200px;
}

.inner:after {
  content: " ";
  display: table;
  clear: both;
}

.block {
  display: flex;
}

.header {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  padding-top: 47px;
}

@media only screen and (max-width: 767px) {
  .header {
    padding-top: 20px;
  }
}

.header__nav {
  position: relative;
  z-index: 1000;
  float: right;
  margin-right: -7px;
  width: 31px;
  height: 31px;
  background: none;
}

.header__nav:before, .header__nav:after {
  content: '';
  position: absolute;
  right: 7px;
  width: 17px;
  height: 3px;
  background-color: #3F566E;
  transform-origin: right center;
  transition: transform 0.25s, background-color .25s;
}

.header_mod .header__nav:before, .header_mod .header__nav:after {
  background-color: #FFDC45;
}

.header__nav.is-active:before {
  transform: translate3d(0px, -3px, 0) rotate(-45deg) scaleX(1.4);
  background-color: #3F566E;
}

.header__nav.is-active:after {
  transform: translate3d(0px, 2px, 0) rotate(45deg) scaleX(1.4);
  background-color: #3F566E;
}

.header__nav:before {
  top: 8px;
}

.header__nav:after {
  bottom: 8px;
}

.logo {
  position: relative;
  z-index: 1000;
  display: inline-block;
  vertical-align: top;
  font-size: 31px;
}

.logo__icon {
  display: block;
  fill: #3F566E;
  transition: fill .25s;
}

.header_mod .logo__icon {
  fill: #fff;
}

.header.is-open .logo__icon {
  fill: #3F566E;
}

.nav {
  text-align: right;
}

@media only screen and (max-width: 767px) {
  .nav {
    text-align: left;
  }
}

.nav__item:not(:last-child) {
