.menu {
  position: fixed;
  z-index: 999;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  padding-top: 100px;
  min-width: 320px;
  background: rgba(250, 250, 250, 0.9);
  box-shadow: 0 24px 55px 0 rgba(0, 0, 0, 0.19);
  text-align: center;
  font-size: 0;
  transform: translate3d(0, -110%, 0);
  transition: transform 0.3s ease-in-out;
}

.menu:before {
  content: "";
  display: inline-block;
  vertical-align: middle;
  width: 0;
  height: 100%;
}

@supports (backdrop-filter: blur(11px)) {
  .menu {
    background: rgba(250, 250, 250, 0.69);
    backdrop-filter: blur(11px);
  }
}

